import React, { useState } from "react";
import SignUpInfo from "./SignUpInfo";
import PersonalInfo from "./PersonalInfo";
import OtherInfo from "./OtherInfo";

import {useNavigate} from 'react-router-dom'

function Form() {

  let navigate=useNavigate();

  const [page, setPage] = useState(0);
  const [formData, setFormData] = useState({
    email: "",
    password: "",
    confirmPassword: "",
    firstName: "",
    lastName: "",
    username: "",
    interests:{

    },
  });

  const handleSubmit=async (e)=>{
    // e.preventDefault();
    console.log("Signup done")
    const response = await fetch(`http://localhost:8080/apis/auth/createUser`, {
        method: "POST", 
        headers: {
          "Content-Type": "application/json",
        },  
        body: JSON.stringify({email:formData.email,password:formData.password,firstName:formData.firstName,lastName:formData.lastName,userName:formData.username,interestsData:formData.interests}),  
      });
    
    const json=await response.json(); 
    // console.log(json)
    if(json.Success){
        localStorage.setItem('token', json.authToken);
        localStorage.setItem('id', json.id);
        navigate('/uploadImage')
    }
    else{
        alert("please give valid details")
    }
}


  const FormTitles = ["Sign Up", "Personal Info",""];

  const PageDisplay = () => {
    if (page === 0) {
      return <SignUpInfo formData={formData} setFormData={setFormData} />;
    } else if (page === 1) {
      return <PersonalInfo formData={formData} setFormData={setFormData} />;
    } else {
      return <OtherInfo formData={formData} setFormData={setFormData} />;
    }
  };

  return (
    <div className="form">
      <div className="progressbar">
        <div
          style={{ width: page === 0 ? "33.3%" : page == 1 ? "66.6%" : "100%" }}
        ></div>
      </div>
      <div className="form-container">
        <div className="header">
          <h1>{FormTitles[page]}</h1>
        </div>
        <div className="body">{PageDisplay()}</div>
        <div className="footer">
          <button
            disabled={page == 0}
            onClick={() => {
              setPage((currPage) => currPage - 1);
            }}
          >
            Prev
          </button>
          <button
            onClick={() => {
              if (page === FormTitles.length - 1) {
                alert("FORM SUBMITTED");
                handleSubmit();
              } else {
                setPage((currPage) => currPage + 1);
              }
            }}
          >
            {page === FormTitles.length - 1 ? "Submit" : "Next"}
          </button>
        </div>
      </div>
    </div>
  );
}

export default Form;